.login {
    border: 2px solid green;
    width: 300px;
    margin: 100px auto 0 auto;
    padding: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.login h2 {
    font-size: 20px;
    font-weight: bold;
    text-align: center;
}
.login form {
    text-align: center;
}
.login form > div {
    padding: 5px;
}
.loginButton {
    /*чтобы перезаписать пересекающиеся свойства из SuperButton для ReduxForm*/
    font-size: 20px !important;
    margin-bottom: 10px;
}

.formErrorBlock {
    height: 20px;
    margin-bottom: 10px;
}
.formError {
    color: red;
    font-size: 16px;
    font-weight: bold;
}